import {useEffect, useRef, useState} from "react"
import {VideoSource} from "~/types/VideoSource"
import {classes} from "~/utilities/classes"

import "./MobileVideoPlayer.css"

interface MobileVideoPlayerProps extends Omit<React.HTMLAttributes<HTMLDivElement>, "onPause"> {
    videoSources: Array<VideoSource> | VideoSource
    videoClass?: string | undefined
    videoStyle?: React.CSSProperties | undefined
    pauseVideo?: boolean
}

const MobileVideoPlayer: React.VFC<MobileVideoPlayerProps> = props => {
    const {className, pauseVideo, videoClass, videoStyle, videoSources, ...otherProps} = props
    const [loaded, setLoaded] = useState(false)
    const videoRef = useRef<HTMLVideoElement | null>(null)

    useEffect(() => {
        if (pauseVideo) {
            videoRef.current?.pause()
        } else {
            videoRef.current?.play()
        }
    }, [pauseVideo])

    const onLoadHandler = () => {
        setLoaded(true)
    }

    return (
        <div className={classes("mobile-video-player-k6zm", loaded ? "visible-k6zm" : "", className)} {...otherProps}>
            <div className="mobile-frame-k6zm">
                <video
                    className={classes("video-k6zm", videoClass)}
                    preload="metadata"
                    autoPlay
                    loop
                    muted
                    playsInline
                    onLoadedData={onLoadHandler}
                    ref={videoRef}
                    style={videoStyle}
                >
                    {[videoSources].flat().map((source, index) => {
                        if (!source.src || !source.mimeType) return
                        return <source src={source.src} type={source.mimeType} key={index} />
                    })}
                </video>
            </div>
        </div>
    )
}

export default MobileVideoPlayer
